// Components
import BaseHeader from '@/components/argon-core/BaseHeader'
import MainTitle from '@/components/shack/MainTitle.vue'
import Table from '@/components/shack/PromotionsTable'

import { mapState } from 'vuex'

export default {
  layout: 'ShackDash',
  components: {
    BaseHeader,
    MainTitle,
    Table
  },
  async asyncData({ store, $promotion, error }) {
    await $promotion
      .getAllPromotions()
      .then(data => {
        store.commit('promotions/setPromotions', data)
      })
      .catch(err => {
        error({
          statusCode: err.statusCode,
          message: err.response
            ? JSON.stringify(err.response.data.errors)
            : err.message
        })
      })
  },
  computed: {
    ...mapState({
      plans: state => state.promotions.promotions
      // meta: state => state.plans.plans.meta,
      // links: state => state.plans.plans.links
    })
  },
  methods: {
    next() {
      const { next } = this.links
      this.$plan
        .getAllPromotions(next)
        .then(({ data }) => {
          store.commit('promotions/setPromotions', data)
        })
        .catch(err => {
          const message = err.response
            ? JSON.stringify(err.response.data.errors)
            : err.message

          this.$bvToast.toast(message, {
            title: 'Error',
            variant: 'danger',
            solid: true
          })
        })
    },
    prev() {
      const { prev } = this.links
      this.$plan
        .getAllPromotions(prev)
        .then(({ data }) => {
          store.commit('promotions/setPromotions', data)
        })
        .catch(err => {
          const message = err.response
            ? JSON.stringify(err.response.data.errors)
            : err.message

          this.$bvToast.toast(message, {
            title: 'Error',
            variant: 'danger',
            solid: true
          })
        })
    }
  }
}
